*
{
  margin: 0;
  padding: 0;
}
.app
{
 position: absolute;
 top: 45%;
 left: 50%;
 transform: translate(-45%, -50%);
 border: 2px solid black;
 width: 60%;
 height: 50vh;
 text-align: center;
 box-shadow: -2px 1px 27px 3px rgba(0,0,0,0.79);
 color: white;
 border-radius: 10px;
}
body
{
  background: url(quiz\ bg\ III.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
}
.Question-text
{ 
  margin-top: 10px;
  margin-bottom: 30px;
  font-size: 2rem;
  font-weight: 500;
  color: whitesmoke;
}
.Answer-section button
{
  padding: 15px 10px;
  font-size: 18px;
  margin: 5px;
  background: linear-gradient(90deg,#72d4d1 0%, #cfdde0 80%);
  border-radius: 7px;
  cursor: pointer;
}
.Answer-section button:hover
{
  transform: scale(0.99);
  opacity: 0.80;
}
.score-section small
{
  position: absolute;
  top: 85%;
  left: 2%;
  border: 2px solid red;
  background: red;
  color: white;
  font-size: 15px;
  padding: 5px 5px;
  border-radius: 5px;
}
.score-section button
{
  background: linear-gradient(90deg,#72d4d1 0%, #cfdde0 80%);
  color: black;
  font-size: 20px;
  padding: 10px 10px;
  border: 0;
  outline: 0;
  border-radius: 5px;
}
.score-section button:hover
{
  transform: scale(0.99);
  opacity: 0.80;
}